import { Component, OnInit, Output, EventEmitter } from '@angular/core';
// import { Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { AuthenticationService } from './../../services/authentication.service';
import { MainServicesService } from './../../services/main-services.service';
import $ from 'jquery';
import { LoaderService } from 'src/app/services/loader.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit {


  loginForm: FormGroup;
  errorMsg: string;
  loginErrorMsg;
  email;
  password;
  loginValue;


  constructor(private fb: FormBuilder,
    private auth: AuthenticationService,
    private router: Router, private main_serives: MainServicesService, private loader:LoaderService ) {


    this.loginForm = this.fb.group({
      email: ['', [Validators.required, Validators.email]],
      password: ['', Validators.required]

    })
    this.email = '';
    this.password = '';
    let checkLogin:any = JSON.parse(localStorage.getItem('login_permission'));
    let checkLoginToken:any = localStorage.getItem('login_token');
    
    if(checkLoginToken){


      this.auth.verifyToken({token:checkLoginToken}).subscribe((data) =>{
       if(data['success']){
          if (checkLogin.role == 'Super Admin') {
            this.router.navigateByUrl('/admin/tokens');
          }else{
            this.router.navigateByUrl('/customer/tokens');
          }
        }
      },
      (error)=>{
        localStorage.clear();
        this.router.navigateByUrl('/login')
      }
      )
     
      
    }
    
  }




  getEmail(e) {
  }
  getPass(e) {
  }

  ngOnInit() {



    this.email = '';
    this.password = '';
    let checkLogin:any = JSON.parse(localStorage.getItem('login_permission'));
    let checkLoginToken:any = localStorage.getItem('login_token');
    
    if(checkLoginToken){


      this.auth.verifyToken({token:checkLoginToken}).subscribe((data) =>{
      
        if(data['success']){
          if (checkLogin.role == 'Super Admin') {
            this.router.navigateByUrl('/admin/tokens');
          }else{
            this.router.navigateByUrl('/customer/tokens');
          }
        }
      },
      (error)=>{
        localStorage.clear();
        
      }
      )
     
      
    }


  }
  ionViewWillEnter() {
   
  }



  login(value) {


    this.auth.signIn(value).subscribe(
      (data) => {
     
        let loginPermissions = {
          // "isAuthenticated": data['permission'],
          "id": data['user']['_id'],
          "name": data['user']['name'],
          "email": data['user']['email'],
          "role": data['user']['role']['name'],
          "notifications": data['notifications'],
          "image": data['user']['image'],
          "phone": data['user']['phone'],
          "gender": data['user']['gender'],
        }

        localStorage.setItem("login_token", data['token']);
        localStorage.setItem('login_permission', JSON.stringify(loginPermissions));

        // this.addNewItem();




        // this.router.navigateByUrl('/admin-p');
        //  this.router.navigateByUrl('/header');


        if (data['user']['role']['name'] == 'Super Admin') {
          
          this.router.navigateByUrl('/admin/tokens');
        }
        
        // else if(data['user']['role']['name'] == 'Admin'){
        //   this.router.navigateByUrl('/admin/home');
        // }

        else {
          this.router.navigateByUrl('/customer/tokens');
        }
        this.main_serives.filter('click');
      },
      (error) => {
        let error_not_exist = error['error'];


        let checkErrorType = typeof error_not_exist.error;

        if (checkErrorType == 'object') {
          this.loginErrorMsg = error_not_exist.error;
        }
        else {
          this.errorMsg = error_not_exist.error;

        }




        /* for(let error of loginErrorMsg.errors){          
           this.errorMsg = error.msg;
         }*/

      }
    )
 

  }

 
submit(e)
  {
    if(e.keyCode == 13){
    $('#login-btn').click();
    }

  }
}
