import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { catchError, retry } from 'rxjs/operators';


import $ from 'jquery';
@Component({
  selector: 'app-admin-layout',
  templateUrl: './admin-layout.component.html',
  styleUrls: ['./admin-layout.component.scss'],
})
export class AdminLayoutComponent implements OnInit {

  leftSize: number;
  rightSize: number;
  showMenu: boolean = false;
  showMenuMob = true;

   userId;
  getPermission;
  role;
  displayTokenAdmin;
  displayTokenCustomer;
  notificationsCount: number;


  constructor() {
    
  }

  ngOnInit() {
    this.getPermission = JSON.parse(localStorage.getItem('login_permission'));
    //  this.userName = this.getPermission.name;
    //  this.profileImage = this.getPermission.image;
     this.notificationsCount = this.getPermission.notifications; 
     this.role = this.getPermission.name;
     this.userId = this.getPermission.id;
     console.log(this.role);
  }

   manuToggle() {
     
    this.showMenu = !this.showMenu;

    if (this.showMenu == false) {      

      $('#firstdiv').removeClass("small-aside");
      $('#seconddiv').removeClass("big-right-side");
      $('#list').removeClass("collapse-active");
      $('.iconpadding').css("padding-right","5px");
      if ($(window).width() < 1221) {
        this.showMenu = true;
        
      }
      // console.log(this.showMenu);
      
    }
    else {      
      $('#firstdiv').addClass("small-aside");
      $('#seconddiv').addClass("big-right-side");
      $('#list').addClass("collapse-active");
      $('.iconpadding').css("padding-right","0px");
      if ($(window).width() < 1221) {
        this.showMenu = false;
        $('#firstdiv').removeClass("small-aside");
        $('#firstdiv').addClass("show-mobile-bar");
      }
      // console.log(this.showMenu);
    }
  }

  hide_mobile_menu() {
    // $('#firstdiv').removeClass("small-aside");  
    $('#firstdiv').removeClass("show-mobile-bar");        
  }
  



  mobileManuToggle() {
    $('#firstdiv').addClass("small-aside");
    // this.showMenuMob = !this.showMenuMob;
  }


  ionViewWillEnter() {
    // if ($(window).width() < 992) {
    //   this.rightSize = 12;
    // }
    // else if ($(window).width() <= 1880) {
    //   // this.leftSize = 2;
    //   this.rightSize = 10;
    // }
    // else {
    //   this.leftSize = 2;
    //   this.rightSize = 10;
    // }
  }

}
