import { Component, OnInit } from '@angular/core';
import * as _moment from 'moment';
import { MainServicesService } from './../../../services/main-services.service';
import { Router } from '@angular/router';
import $ from 'jquery';
import { JoyrideService } from 'ngx-joyride';
import { JoyrideOptions } from 'ngx-joyride/lib/models/joyride-options.class';
import { LoaderService } from 'src/app/services/loader.service';


const moment = _moment; 

@Component({
  selector: 'app-team-tokens-component',
  templateUrl: './team-tokens.component.html',
  styleUrls: ['./team-tokens.component.scss'],
})
export class TeamTokensComponent implements OnInit {

  isLoader:boolean = true;
  pageCounts=Array();
  pageCount:number;
  page:number;
  myTokenPage: boolean;
  list = [];
  pageName= "teamToken";
  href:any; 
  searchText;

  stepcontent =[
    'Start generating a new token',
    'Go here to view the list of tokens assigned to the responsible person',
    'Go here to view the list of tokens assigned as a reviewer',
    'Go here to view the list of tokens assigned to the support person',
    'Click to view the token in detail'
  ]  

  constructor(private tokenService: MainServicesService, 
    private readonly joyrideService:JoyrideService,
    private router: Router,private loader:LoaderService) { }

    startTour(){
      const options: JoyrideOptions = {
        steps:['step1','step2']
      };
      this.joyrideService.startTour(options);
    }

    startMobileTour(){
      const options: JoyrideOptions = {
        steps:['stepC1','step2']
      };
      this.joyrideService.startTour(options);
    }
  
  ngOnInit() {
    $(document).ready(function(){
      $('.mat-sort-header-content').css({
        'margin':'auto'
      })
      $('.mat-sort-header-indicator, .mat-sort-header-stem').css({
        'color':'#fff'
      })
    })

    this.myTokenPage= false;        
    this.tokenService.teamTokensList().subscribe(
      (data) =>{
        this.list= [];
        this.pageCounts= [];
        // this.isLoader = false;
        this.pageCount  = data['pageCount'];
        this.page       = data['page'];
        for(let i=1; i<=data['pageCount']; i++)
        {
          this.pageCounts.push(i);
        }
        data['tokens'].forEach((token) => {
          this.list.push({
            id        : token._id,
            token_no  : '#'+token.token_no,
            heading   : token.heading,
            description: token.description,
            createdAt : moment(token.createdAt).format('DD-MMM-YYYY'),
            timeline  : Math.floor(token.time_period.percentage),
            completed : token.time_period.completed == "1 Days"? "1 Day":token.time_period.completed,
            remaining : token.time_period.remaining == "1 Days"? "1 Day":token.time_period.remaining,
            overdue   : token.time_period.overdue == "1 Days"? "1 Day":token.time_period.overdue,
            priority  : token.priority ? token.priority.name : "",
            status    : token.status,
            user      : token.user['name'],
            createdAtTime: moment(token.createdAt).format('HH:mm'),
            expectedAt: token.expected_date? moment(token.expected_date).format('DD-MMM-YYYY'): '00-00-000',
            expectedAtTime: token.expected_date? moment(token.expected_date).format('HH:mm'): '00-00'
          })
        });
      },
      (error) => {
        // this.isLoader = false;
        console.log(error);
      }
    );
  }

  
  nextPageTeamTokenData(pageNo){
    // this.isLoader = true;
    this.myTokenPage= true;    
    this.tokenService.nextTeamTokenPage(pageNo).subscribe(
      (data) =>{
        this.list= [];
        // this.isLoader = false;
        this.page= data['page'];
        data['tokens'].forEach((token) => {
          this.list.push({
            id        : token._id,
            token_no  : '#'+token.token_no,
            heading   : token.heading,
            description: token.description,
            createdAt : moment(token.createdAt).format('DD-MMM-YYYY'),
            timeline  : Math.floor(token.time_period.percentage),
            completed : token.time_period.completed == "1 Days"? "1 Day":token.time_period.completed,
            remaining : token.time_period.remaining == "1 Days"? "1 Day":token.time_period.remaining,
            overdue   : token.time_period.overdue == "1 Days"? "1 Day":token.time_period.overdue,
            priority  : token.priority ? token.priority.name : "",
            status    : token.status,
            user      : token.user['name'],
            createdAtTime: moment(token.createdAt).format('HH:mm'),
            expectedAt: token.expected_date? moment(token.expected_date).format('DD-MMM-YYYY'): '00-00-000',
            expectedAtTime: token.expected_date? moment(token.expected_date).format('HH:mm'): '00-00'
          })
        });
      },
      (error) => {
        // this.isLoader = false;
        console.log(error);
      }
    );
  }
  
  
  searching(pageName, searchText){
    // this.isLoader = true;        
    this.tokenService.searchTokenApi(pageName, searchText).subscribe(
      (data) =>{
        this.list= [];
        this.pageCounts= [];
        // this.isLoader = false;
        this.pageCount  = data['pageCount'];
        this.page       = data['page'];
        for(let i=1; i<=data['pageCount']; i++)
        {
          this.pageCounts.push(i);
        }
        data['tokens'].forEach((token) => {
          this.list.push({
            id        : token._id,
            token_no  : '#'+token.token_no,
            heading   : token.heading,
            description: token.description,
            createdAt : moment(token.createdAt).format('DD-MMM-YYYY'),
            timeline  : Math.floor(token.time_period.percentage),
            completed : token.time_period.completed == "1 Days"? "1 Day":token.time_period.completed,
            remaining : token.time_period.remaining == "1 Days"? "1 Day":token.time_period.remaining,
            overdue   : token.time_period.overdue == "1 Days"? "1 Day":token.time_period.overdue,
            priority  : token.priority ? token.priority.name : "",
            status    : token.status,
            user      : token.user['name'],
            createdAtTime: moment(token.createdAt).format('HH:mm'),
            expectedAt: token.expected_date? moment(token.expected_date).format('DD-MMM-YYYY'): '00-00-000',
            expectedAtTime: token.expected_date? moment(token.expected_date).format('HH:mm'): '00-00'
          })
        });
      },
      (error)=>{
        // this.isLoader = false;
        console.log(error);
        
      }
      )
    
  }

  generateFake(count: number): Array<number> {
    const indexes = [];
    for (let i = 0; i < count; i++) {
      indexes.push(i);
    }
    return indexes;
  }

}
