import { Component, OnInit } from '@angular/core';
import { ModalController } from "@ionic/angular";
import { NavParams } from '@ionic/angular';
import { MainServicesService } from './../../services/main-services.service';
import $ from 'jquery';



@Component({
  selector: 'app-modalpopup',
  templateUrl: './modalpopup.component.html',
  styleUrls: ['./modalpopup.component.scss'],
})
export class ModalpopupComponent implements OnInit {

  userId;
  userName;
  userRole;
  getPersmission;
  userEmail;
  userPhone;
  userGender;
  userImage;
  userBranch;
  userDept;
  userList;
  list=[];
  showFields:boolean = true;
  userData;
  showBtn;
  filesToUpload: any[] = [];
  selectedFile;
  audio;
  isLoader=false;

  constructor(private modalController: ModalController,private navParams:NavParams,private profileData: MainServicesService
    ) {

    this.getPersmission = JSON.parse(localStorage.getItem('login_permission'));
    this.userId = this.getPersmission.id;
   
   }

  ngOnInit() {  }


  ionViewWillEnter(){
    this.isLoader=true;
    this.list=[];
    this.profileData.getUserDetails(this.userId).subscribe(
      (result) =>{
        this.isLoader=false;
        this.userList = result['user'];
        this.userName = result['user']['name'];
        this.userImage = result['user']['image']? result['user']['image']['url']: '';
        this.userRole = result['user']['role']['name'];
        this.userBranch = result['user']['branch']? result['user']['branch']['name']: '';
        this.userDept = result['user']['department']? result['user']['department']['name']: '';
        this.userEmail = result['user']['email'];
        this.userPhone = result['user']['phone'];
        this.userGender = result['user']['gender'];
        
        
        
       this.userData = {
          _id: this.userList._id,
          name: this.userList.name,
          image: this.userList.image? this.userList.image.url: '',
          role_id:this.userList.role._id,
          role:this.userList.role.name,
          branch_id:this.userList.branch? this.userList.branch._id : '',
          department_id:this.userList.department? this.userList.department._id : '' ,
          email:this.userList.email,
          phone:this.userList.phone,
          gender:this.userList.gender
        }
        
      }
    )
  }

  tokenImage(event){
    this.showBtn = true;
    console.log(event);
    
    this.selectedFile = event.target.files[0];

    
  
    this.filesToUpload.push(this.selectedFile);
    console.log(this.filesToUpload);
    var fileName = this.selectedFile.name;
    var extension = fileName.split('.').pop();

    var reader = new FileReader();
    console.log(reader);
    

    
  if(extension == 'jpg' || extension == 'png' || extension == 'jpeg' || extension == 'gif' || extension == 'webp')
  {
    reader.onload = (e:any) =>{
      $('#blah').attr('src', e.target.result);
    
    }
  }

  reader.readAsDataURL(this.selectedFile); // convert to base64 string


  }
  submit(){
 
    // let addDetails;

    // addDetails = {
    //   name:this.userData.name,
    //   phone:this.userData.phone,
    //   email:this.userData.email,
    //   gender:this.userData.gender,
    //   role:this.userData.role_id,

    //   branch:this.userData.branch_id? this.userData.branch_id : '',
    //   department:this.userData.department_id? this.userData.department_id : '',
    //   image:this.selectedFile,

    // }
    // console.log(addDetails)

    const formData = new FormData();
    // formData.append('name', addDetails.name);
    // formData.append('phone', addDetails.phone);
    // formData.append('email', addDetails.email);
    // formData.append('gender', addDetails.gender);
    // formData.append('role', addDetails.role);
    // formData.append('branch', addDetails.branch);
    // formData.append('department', addDetails.department);
    formData.append('image',this.selectedFile);

    this.profileData.updateUserDetails(this.userId,formData).subscribe(
      (result)=>{
        console.log(result);
        
        this.modalController.dismiss(result, 'success');
      }
    )

  }

  closeModal(){
    this.modalController.dismiss();
  }
  dismiss() {
    this.modalController.dismiss({
      'dismissed': true
    });
  }
}
