import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { AuthGuardGuard } from "./login-authentication/guard/auth-guard.guard";
import { SuperAdminAuthGuard } from "./login-authentication/guard/super-admin-auth.guard";
import { FooterComponent } from './common/footer/footer.component';
import { AsideComponent } from './common/aside/aside.component';
import { LogComponent } from './common/log/log.component';

import { AddBranchModalComponent } from './admin/branch/add-branch-modal/add-branch-modal.component';
import { AddRoleModelComponent } from './admin/role/add-role-model/add-role-model.component';
import { AddDepartmentModalComponent } from './admin/department/add-department-modal/add-department-modal.component';
import { AddUserModalComponent } from './admin/user/add-user-modal/add-user-modal.component';
import { EditRoleModalComponent } from './admin/role/edit-role-modal/edit-role-modal.component';
import { HeaderRightSideComponent } from './common/header-right-side/header-right-side.component';
import { HeaderComponent } from './common/header/header.component';

import { LoginComponent } from './login-authentication/login/login.component';


import { AdminLayoutComponent } from './layout/admin-layout/admin-layout.component';
import { TokensComponent } from './admin/tokens/tokens.component';

import { MyTokensComponent } from './common/tokens-component/my-tokens/my-tokens.component';
import { TeamTokensComponent } from './common/tokens-component/team-tokens/team-tokens.component';
import { AllTokensComponent } from './common/tokens-component/all-tokens/all-tokens.component';


import { CustomerLayoutComponent } from './layout/customer-layout/customer-layout.component';


const routes: Routes = [
 
  {
    path:'',
    pathMatch:'full',
    redirectTo:'login'
  },
  {
    path:'login',
    component : LoginComponent
  },
  {
    path : 'customer',
    component: CustomerLayoutComponent,
    // canActivate: [AuthGuardGuard],
    children : [
   
      {
        path :'tokens',           
        // component:DeptComponent,            
        component:MyTokensComponent,
        pathMatch:'full',
        canActivate: [AuthGuardGuard],
      }, 
      {
        path :'team-tokens',                             
        component:TeamTokensComponent,    
        canActivate: [AuthGuardGuard],
      }, 
      {
        path :'all-tokens',                      
        component:AllTokensComponent,
        canActivate: [AuthGuardGuard]           
      },
      {
        path: 'add-token',
        loadChildren: () => import('./token/add-token/add-token.module').then( m => m.AddTokenPageModule),
        canActivate: [AuthGuardGuard] 
      },
      {
        path: 'edit-token/:id',
        loadChildren: () => import('./token/edit-token/edit-token.module').then( m => m.EditTokenPageModule),
        canActivate: [AuthGuardGuard] 

      },

    ]
  },

  {
    path: 'add-token',
    loadChildren: () => import('./token/add-token/add-token.module').then( m => m.AddTokenPageModule),
    canActivate: [AuthGuardGuard] 
  },
  {
    path: 'edit-token/:id',
    loadChildren: () => import('./token/edit-token/edit-token.module').then( m => m.EditTokenPageModule),
    canActivate: [AuthGuardGuard] 

  },


  {
    path: 'admin',
    component: AdminLayoutComponent, 
    canActivate: [SuperAdminAuthGuard],
    // loadChildren: () => import('./home/home.module').then( m => m.HomePageModule),
    children: [
      {
        path: 'home',            
        loadChildren: () => import('./admin/home/home.module').then( m => m.HomePageModule),
        canActivate: [SuperAdminAuthGuard],
        pathMatch:'full'
      },

      {
        path: 'departments',            
        loadChildren: () => import('./admin/department/departments-list/departments-list.module').then( m => m.DepartmentsListPageModule),
        canActivate: [SuperAdminAuthGuard],
      },
      {
        path: 'roles',                    
        loadChildren: () => import('./admin/role/roles-list/roles-list.module').then( m => m.RolesListPageModule),  
        canActivate: [SuperAdminAuthGuard],      
      },
      {
        path: 'branches',            
        loadChildren: () => import('./admin/branch/branches-list/branches-list.module').then( m => m.BranchesListPageModule), 
        canActivate: [SuperAdminAuthGuard],       
      },      
      {
        path: 'token-types',            
        loadChildren: () => import('./admin/token-type/token-types-list/token-types-list.module').then( m => m.TokenTypesListPageModule), 
        canActivate: [SuperAdminAuthGuard],      
      },           
      {
        path: 'users',            
        loadChildren: () => import('./admin/user/users-list/users-list.module').then( m => m.UsersListPageModule),
        canActivate: [SuperAdminAuthGuard],
      },
      {
        path: 'priorities',            
        loadChildren: () => import('./admin/priorities/priorities-list/priorities-list.module').then( m => m.PrioritiesListPageModule),
        canActivate: [SuperAdminAuthGuard],
      },
      {
        path: 'tokens',
        component: TokensComponent,
        children:[
          {
            path :'',           
            // component:DeptComponent,            
            component:MyTokensComponent,
            pathMatch:'full'
          }, 
          {
            path :'all',                      
            component:AllTokensComponent           
          } ,
          {
            path: 'add-token',
            loadChildren: () => import('./token/add-token/add-token.module').then( m => m.AddTokenPageModule),
            canActivate: [SuperAdminAuthGuard] 
          },
          {
            path: 'edit-token/:id',
            // component:EditTokenComponent,
            loadChildren: () => import('./token/edit-token/edit-token.module').then( m => m.EditTokenPageModule),
            canActivate: [SuperAdminAuthGuard] 

          },
          


        ]

        // loadChildren:()=>

        // loadChildren: () => import('./admin/token/my-token/my-token.module').then( m => m.MyTokenPageModule)
      },
     
      
    ]

    /*children: [
      { 
        path: '', component: DeptComponent, pathMatch: 'full' 
      },
      { 
          path: '',
          loadChildren: () => import('./department/departments-list/departments-list.module').then( m => m.DepartmentsListPageModule),
          pathMatch: 'full' 
        }
        
    ]*/
  },
  {
     path :'m-t',
    component:MyTokensComponent,
  },
  {
    path: 'footer',
    component : FooterComponent
  },
  
  // {
  //   path: 'add-branch-modal',
  //   component : AddBranchModalComponent
  // },
  // {
  //   path: 'add-role-modal',
  //   component : AddRoleModelComponent
  // },
  // {
  //   path: 'add-department-modal',
  //   component : AddDepartmentModalComponent
  // },
  // {
  //   path: 'add-user-modal',
  //   component : AddUserModalComponent
  // },
  // {
  //   path: 'edit-role-modal',
  //   component : EditRoleModalComponent
  // },
  {
    path: 'log',
    component : LogComponent
  },
  {
    path: 'header-right-side',
    component : HeaderRightSideComponent
  },
  {
    path: 'header',
    component : HeaderComponent
  },
  {
    path: 'forget-password',
    loadChildren: () => import('./login-authentication/forget-password/forget-password.module').then( m => m.ForgetPasswordPageModule)
  },
  {
    path: 'new-password/:id',
    loadChildren: () => import('./login-authentication/new-password/new-password.module').then( m => m.NewPasswordPageModule)
  },  
  {
    path: 'aside',
    component:AsideComponent
  },
  {
    path: 'priorities-list',
    loadChildren: () => import('./admin/priorities/priorities-list/priorities-list.module').then( m => m.PrioritiesListPageModule)
  },

 



 
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
