import { Component, OnInit } from '@angular/core';
import { ModalController } from "@ionic/angular";
import { ModalpopupComponent } from "./../../common/modalpopup/modalpopup.component";
import { Router } from  '@angular/router';
import { MainServicesService } from './../../services/main-services.service';
import { AuthenticationService } from './../../services/authentication.service';



@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit {
  notificationsCount: number;
  values: any;
  unseenTokens = [];

  constructor(private router: Router,private modalController: ModalController, private notificationService: MainServicesService, private auth : AuthenticationService, ) {
    this.values = JSON.parse(localStorage.getItem("login_permission"));
    this.notificationsCount = this.values.notifications;    
  }

  openModal(){
    this.modalController.create({component:ModalpopupComponent}).then((modalElement)=>
    {
      modalElement.present();
    })
  }
  logout(){

  
  	this.auth.signout().subscribe(
  		(data)=>{
        localStorage.clear();
        this.router.navigateByUrl('/login');
  		},
  		(error)=>{
        localStorage.clear();
        this.router.navigateByUrl('/login');
  			console.log(error);
  		}
	)
  }
  
  ngOnInit() {}
  
  getNotifications(){
    this.notificationService.notifications().subscribe(
      (data) => {
        data['tokens']['token_data']['unseen'].forEach(unseen => {
          this.unseenTokens.push({
            id          : unseen._id,
            heading     : unseen.heading,
            token_type  : unseen.token_type,
            priority    : unseen.priority? unseen.priority : 'High',
            token_no    : unseen.token_no,
            status      : unseen.status
          })
        });     
      },
      (error) => {
        console.log(error);
      }
    )
  }

}
