import { Component, OnInit ,Input, Output, EventEmitter} from '@angular/core';

import { MatTab } from '@angular/material/tabs';

import $ from 'jquery';

@Component({
  selector: 'app-log',
  templateUrl: './log.component.html',
  styleUrls: ['./log.component.scss'],
})
export class LogComponent implements OnInit {

	isActive : boolean=true;

	selectedIndex=0;


	 @Input() actionlog;
	 @Input() comment;
	 @Input() tokenNo1;
	
	
	tnumber;

	comments;
	commentImages



	checkIfAdmin:boolean=false;

	constructor() {

		let check=JSON.parse(localStorage.getItem('login_permission'))
		if(check['role']['name'])
		{
			this.checkIfAdmin=true;
		}
		else{
			this.checkIfAdmin=false;
		}
	
	 }
	 
  ngOnInit() {}
  ionViewWillEnter() {

	  
	  this.comments = this.comment;


  	$(document).ready(function(){
  		$('#mat-tab-label-0-0').addClass('active-tab-action-log');
  		// $('#mat-tab-label-0-0').click(function(){  			
  		// 	$('#mat-tab-label-0-0').addClass('active-tab');
  		// });
  		$('#mat-tab-label-0-1').click(function(){  			
  			$('#mat-tab-label-0-0').removeClass('active-tab-action-log');
  		});
  	});
  	
  	
  }

  removeActiveTabStyle(){
  	// alert('d');
  	$(document).ready(function(){
  		$('#mat-tab-label-0-0').css({
	  		"border-bottom": "solid 0px #58a0bb",
	    	"opacity": "0"
	  	})	
  	})
  }



}
