import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams, HttpErrorResponse } from '@angular/common/http';
import { map, catchError, subscribeOn } from 'rxjs/operators';
import { Observable, throwError } from 'rxjs';
import { Router } from "@angular/router";



@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {
  
    // url="http://localhost:3000/api/";
  //  url="http://192.168.43.254:3000/api/";
  url = "https://kolonizer.net/backend/api/";          // AWS API

  // url = "http://18.218.81.42/backend/api/";          // AWS API
  // url = "https://kolonizer.net/backend/api/"




  isLogin = false;
  isSuperAdmin = false;
 
  errorHandler(error: HttpErrorResponse) {
    return throwError(error.message || "Sever Error");
  }



  constructor(private http: HttpClient, private router : Router) { 
  }
    

  // this code is use for login

  sign_in_url=this.url+'signin';

  signIn(sign_values){    
    return this.http.post(this.sign_in_url,sign_values).pipe(
      map(
          (res) => res
      )
    );
  }
  
  // this code is use for Logout
  sign_out_url=this.url+'signout';

  signout(){
    
    let bearerToken = localStorage.getItem('login_token');
    let httpOptions = {
      headers: new HttpHeaders({       
        'Content-Type':  'application/json' ,
         'Authorization' : 'Bearer '+bearerToken,
        'Accept': '*/*'         
      })
    }; 

    return this.http.get(this.sign_out_url,httpOptions);
  }


  verifyTokenUrl=this.url+'verifyToken';
  verifyToken(token){
    return this.http.post(this.verifyTokenUrl, token).pipe(map(res => res));
  }


  isLoggedIn() {
    const token = localStorage.getItem('login_token');
    
    console.log(token);
    
    if (token)
    {
      this.isLogin = true;
      this.verifyToken({token:token}).subscribe(
        (result)=>{
          console.log(result); 
      
        },
        (error)=>{
          console.log(error['error']['success']);
          if(error['error']['success'] ==  false){
            console.log('no data');
            localStorage.clear();
            window.location.href = "/login";

          }
        }
      )
    }
    else{
      this.isLogin = false;
    }
    return this.isLogin;
  }

  isRoleSuperAdmin() {
   
    const userData:any = JSON.parse(localStorage.getItem('login_permission'));
    if (userData.role == 'Super Admin'){
      this.isSuperAdmin = true;
    }else{
      this.isSuperAdmin = false;
    }
    return this.isSuperAdmin;
  }
}
 