import { Component } from '@angular/core';

import { Platform } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent {

  leftSize:number;
  showMenuMob = true;



    public appPages = [
    {
      title: 'Dashboard',
      url: '/dashboard',
      icon: 'speedometer'
    },
    {
      title: 'First',
      // url: '/team-meeting-lists',
      icon: 'contacts',
      subpages: [
        { title: 'Team Meeting', url: '/team-meeting-lists',icon : 'contacts' },
        { title: 'Upcomming Meeting', url: '/upcoming-meeting',icon : 'bicycle' },
        // { title: 'Prev. Team Meeting', url: '/previous-meeting' }
      ]
     },
     {
      title: 'Sec',
      url: '/dept-report',
      icon: 'clipboard'

    },
    {
      title: 'Third',
      url: '/department',
      icon: 'contract',
      // icon: 'snow'
    },
    {
      title: 'four',
      url: '/employee',
      icon: 'people'
    },
   
    
  ];
  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar
  ) {
    this.initializeApp();
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }






  

}
