import { Component, OnInit } from '@angular/core';
import { ModalController } from "@ionic/angular";
import { ModalpopupComponent } from "./../../common/modalpopup/modalpopup.component";
import { Router } from  '@angular/router';
import { MainServicesService } from './../../services/main-services.service';
import { AuthenticationService } from './../../services/authentication.service';
import  $ from 'jquery';
import {PopoverController } from '@ionic/angular';
import { MobileNotificationPopupComponent } from "./../../common/mobile-notification-popup/mobile-notification-popup.component";
import { PopupComponent} from './../../common/popup/popup.component';
import { ToastrService } from 'ngx-toastr';


@Component({
  selector: 'app-customer-layout',
  templateUrl: './customer-layout.component.html',
  styleUrls: ['./customer-layout.component.scss'],
})
export class CustomerLayoutComponent implements OnInit {

  notificationsCount: number;
  values: any;
  unseenTokens = [];
  show_991:boolean = false;
  userName;
  profileImage;
  userId;
  role;
  displayTokenAdmin;
  displayTokenCustomer;
  sucmsg;

  showMessage;
  notifications;
  num;
  tokenCount:number;

  constructor(private router: Router,private modalController: ModalController, private notificationService: MainServicesService,
    private toastr: ToastrService, private auth : AuthenticationService, private popctrl: PopoverController) {
      this.notificationService.listen().subscribe(
        (m:any)=>{
          if(this.router.url.includes('/customer/'))
          {
            console.log(m);
          this.ngOnInit();
          }
        }
      )

      this.notificationService.tokenListen().subscribe(
        (m:any)=>{
           console.log(m);    
           this.ngOnInit();
        }
      )
         
  }

    
  ngOnInit() {
    this.values = JSON.parse(localStorage.getItem("login_permission"));
    this.userId = this.values.id;
    this.notifications = this.values.notifications;    
    this.userName = this.values.name;
    this.profileImage = this.values.image? this.values.image.url: '';

    this.notificationService.notifications().subscribe(
      (data) => {
        this.num=data['tokens']['token_data']['unseen']
        for (this.tokenCount = 0; this.tokenCount < this.num.length; this.tokenCount++) {
          
        }
        console.log(this.tokenCount);
      
      },
      (error) => {
        console.log(error);
      }
    )
    
     this.notificationService.getUserDetails(this.userId).subscribe(
      (result)=>{
      this.userName = result['user']['name'];
      this.profileImage = result['user']['image']? result['user']['image']['url']: '';
      this.notifications = result['notifications'];
      
      },
      error=>{
        console.log(error);
        
      }
      )

    if ($(window).width() < 992) { 
      this.show_991 = true;
    }
   
   
  }
  ionViewWillEnter(){
 
    $('.btn').on('click', function() {
      $('.badgeFirst').removeClass('badgeSecond');
      $('.btn').removeClass('active-tab');
      $(this).addClass('active-tab');
    })
    $('.footer-notification').on('click', function(this:any){
      $('.badgeFirst').addClass('badgeSecond');
    });
   
    this.values = JSON.parse(localStorage.getItem("login_permission"));
    this.userId = this.values.id;
    this.notifications = this.values.notifications;    
    this.userName = this.values.name;
    this.profileImage = this.values.image? this.values.image.url: '';
  }

    
  async getMobileNotifications(ev: any) {
    if(this.tokenCount>0)
    {
      const popover = await this.popctrl.create({
        component: MobileNotificationPopupComponent,
        cssClass: 'mobile-notification-class',
        event: ev,
        translucent: true
      });
      return await popover.present();
    }
  
  }


 

  async openModal() {
    const modal = await this.modalController.create({
      component: ModalpopupComponent,
      cssClass: 'my-profile-custom-class'
    });
    await modal.present();

    const { data: result, role } = await modal.onWillDismiss();

    this.sucmsg = result['message'];


    if (result['success'] == true) {
      this.showMessage = true;
      this.toastr.success(this.sucmsg);
      this.ngOnInit();
    }
  }
  
 
  logout(){

  
  	this.auth.signout().subscribe(
  		(data)=>{
        localStorage.clear();
        this.router.navigateByUrl('/login');
  		},
  		(error)=>{
  			console.log(error);
  		}
	)
  }
  async openpopup(ev:any){
    const popover= await this.popctrl.create({
     component: PopupComponent,
     event:ev 
    })
    return await popover.present()
  }



  
  getNotifications(){
    this.unseenTokens=[];
    this.notificationService.notifications().subscribe(
      (data) => {
        data['tokens']['token_data']['unseen'].forEach(unseen => {
          this.unseenTokens.push({
            id          : unseen._id,
            heading     : unseen.heading,
            token_type  : unseen.token_type,
            priority    : unseen.priority? unseen.priority : 'High',
            token_no    : unseen.token_no,
            status      : unseen.status
          })
        });
      },
      (error) => {
        console.log(error);
      }
    )
  }
 


}
