  stepcontent =[
    'Start generating a new token',
    'Go here to view the list of tokens assigned to the responsible person',
    'Go here to view the list of tokens assigned as a reviewer',
    'Go here to view the list of tokens assigned to the support person',
    'Click to view the token in detail'
  ]  
  


  constructor(private tokenService: MainServicesService,
    private readonly joyrideService:JoyrideService,
     private router: Router,private route: ActivatedRoute, private loader:LoaderService) {
    this.tokenService.tokenListen().subscribe(
      (m:any)=>{
        // if(this.router.url.includes('/admin/tokens/'))
        // {
         console.log(m);    
         this.reviewerTokensList();
        // }
       
      }
    )
    
  }

  ngOnInit() {
    
   
    $('.nik').on('click', function() {
      $('.nik').removeClass('active');
      $(this).addClass('active'); 
    })
  	$(document).ready(function(){
      $('.mat-sort-header-content').css({
        'margin':'auto'
      })
      $('.mat-sort-header-indicator, .mat-sort-header-stem').css({
        'color':'#fff'
      })
    })

  this.responsibleToken();

  }

  startTour(){
    const options: JoyrideOptions = {
      steps:['step1','step2','step3','step4']
    };
    this.joyrideService.startTour(options);
  }

  startMobileTour(){
    const options: JoyrideOptions = {
      steps:['stepM1','stepM2']
    };
    this.joyrideService.startTour(options);
  }

  customerMobileTour(){
    const options: JoyrideOptions = {
      steps:['stepC1','stepC2']
    };
    this.joyrideService.startTour(options);
  }

  ionViewWillEnter(){
    if($('#responsible-tab').hasClass('active')){
      this.responsibleToken();
    }
    else if($('#reviewer-tab').hasClass('active')){
      this.reviewerTokensList();

    }
    else{
      this.supportTokensList();
    }
   
    
  // this.responsibleToken();
  this.href = this.router.url;
  if(this.href.includes('/admin/')){
    $(".token-status-col img").addClass("token-col-shift-left-second");
    
  }

  }

  nextPageData(pageNo){

    // this.isLoader = true;
    this.pageName= "responsibleToken";
    this.myTokenPage= true;
    // this.list= [];
    this.tokenService.nextTokenApi(pageNo).subscribe(
      (data) =>{
        // this.isLoader = false;
        this.list= [];
        this.page= data['page'];
        data['tokens'].forEach((token) => {
          this.list.push({
            id        : token._id,
            token_no  : '#'+token.token_no,
            heading   : token.heading,
            description: token.description,
            createdAt : moment(token.createdAt).format('DD-MMM-YYYY'),
            timeline  : Math.floor(token.time_period.percentage),
            completed : token.time_period.completed == "1 Days"? "1 Day":token.time_period.completed,
            remaining : token.time_period.remaining == "1 Days"? "1 Day":token.time_period.remaining,
            overdue   : token.time_period.overdue == "1 Days"? "1 Day":token.time_period.overdue,
            priority  : token.priority ? token.priority.name : "",
            status    : token.status,
            user      : token.user['name'],
            createdAtTime: moment(token.createdAt).format('HH:mm'),
            expectedAt: token.expected_date? moment(token.expected_date).format('DD-MMM-YYYY'): '00-00-000',
            expectedAtTime: token.expected_date? moment(token.expected_date).format('HH:mm'): '00-00'
          })
        });
      },
      (error) => {
        // this.isLoader = false;
        console.log(error);
      }
      )
  }

  responsibleToken(){  
    $('#responsible-tab').addClass("active");
    $('#reviewer-tab').removeClass("active");
    $('#support-tab').removeClass("active");

    // this.isLoader = true;


    // this.isLoader = true;
    this.href = this.router.url;
    
    
    this.pageName= "responsibleToken";
    this.myTokenPage= true;
    this.pageCounts= [];
    
    this.tokenService.responsiblePersonTokensList().subscribe(
      (data) =>{
        // this.isLoader = false;
        this.pageCounts= [];
        this.list= [];
        this.pageCount  = data['pageCount'];
        this.page       = data['page'];
        for(let i=1; i<=data['pageCount']; i++)
        {
          this.pageCounts.push(i);
        }
        data['tokens'].forEach((token) => {
          this.list.push({
            id        : token._id,
            token_no  : '#'+token.token_no,
            heading   : token.heading,
            description: token.description,
            createdAt : moment(token.createdAt).format('DD-MMM-YYYY'),
            timeline  : Math.floor(token.time_period.percentage),
            completed : token.time_period.completed == "1 Days"? "1 Day":token.time_period.completed,
            remaining : token.time_period.remaining == "1 Days"? "1 Day":token.time_period.remaining,
            overdue   : token.time_period.overdue == "1 Days"? "1 Day":token.time_period.overdue,
            priority  : token.priority ? token.priority.name : "",
            status    : token.status,
            user      : token.user['name'],
            createdAtTime: moment(token.createdAt).format('HH:mm'),
            expectedAt: token.expected_date? moment(token.expected_date).format('DD-MMM-YYYY'): '00-00-000',
            expectedAtTime: token.expected_date? moment(token.expected_date).format('HH:mm'): '00-00'
          })
          
        });
      },
      (error) => {
        // this.isLoader = false;
        console.log(error);
      }
      
    );
  }

