import { Component, OnInit } from '@angular/core';
import { MainServicesService } from './../../services/main-services.service';
import {PopoverController } from '@ionic/angular';
import { Router } from  '@angular/router';
import $ from 'jquery';




@Component({
  selector: 'app-mobile-notification-popup',
  templateUrl: './mobile-notification-popup.component.html',
  styleUrls: ['./mobile-notification-popup.component.scss'],
})
export class MobileNotificationPopupComponent implements OnInit {
  unseenTokens;
  notificationLength;
  href;
  IsEditToken;

  constructor(private router: Router, private notificationService: MainServicesService,  private popctrl: PopoverController) { 
    
  }
    
  async DismissClick() {
    await this.popctrl.dismiss();
    this.notificationService.filter('click');
    }

  ngOnInit() {
    this. getNotifications();
    this.href = this.router.url;
    let checkEdit = this.href.split("/");
    checkEdit['1'] == 'admin'?this.IsEditToken = '/'+checkEdit['1']+'/'+checkEdit['2']+'/'+checkEdit['3'] : this.IsEditToken = '/'+checkEdit['1']+'/'+checkEdit['2'];

   
  }

  getNotifications(){
    this.unseenTokens=[];
    this.notificationService.notifications().subscribe(
      (data) => {
        
        data['tokens']['token_data']['unseen'].forEach(unseen => {
          this.unseenTokens.push({
            id          : unseen._id,
            heading     : unseen.heading,
            token_type  : unseen.token_type,
            priority    : unseen.priority? unseen.priority : 'High',
            token_no    : unseen.token_no,
            status      : unseen.status
          })
        }); 
        this.notificationLength = this.unseenTokens.length;    
        if(this.notificationLength==1)
        {
          $(".popover-content.sc-ion-popover-md").addClass("notification1");
        }
        else if(this.notificationLength==2)
        {
          $(".popover-content.sc-ion-popover-md").addClass("notification2");
        }
        else 
        {
          $(".popover-content.sc-ion-popover-md").addClass("notification3");
        }
      

      },
      (error) => {
        console.log(error);
      }
    )

    
  }
  

}
