 //Anwar

  getUserApi(url){                                            // Get All Data

    let bearerToken = localStorage.getItem('login_token');

    let httpOptions = {
      headers: new HttpHeaders({       
        'Content-Type':  'application/json' ,
        'Authorization' : 'Bearer '+bearerToken,
        'Accept': 'application/json'         
      })
    };  
    
    return this.http.get(url, httpOptions);
  }

  postUserApi(url, formData){                                            // Add Data
    let bearerToken = localStorage.getItem('login_token');
    let httpOptions = {
      headers: new HttpHeaders({       
        'Content-Type':  'application/json' ,
        'Authorization' : 'Bearer '+bearerToken,
        'Accept': 'application/json'         
      })
    };
    return this.http.post(url, formData, httpOptions);
  }


  getSingleUserApi(url, id){                                            // Get Single Data
    let bearerToken = localStorage.getItem('login_token');

    let httpOptions = {
      headers: new HttpHeaders({       
        'Content-Type':  'application/json' ,
        'Authorization' : 'Bearer '+bearerToken,
        'Accept': 'application/json'         
      })
    };
    return this.http.get(url+'/'+id, httpOptions);
  }

  deleteUserApi(url,id){                                            // delete by id data
    let bearerToken = localStorage.getItem('login_token');

    let httpOptions = {
      headers: new HttpHeaders({       
        'Content-Type':  'application/json' ,
        'Authorization' : 'Bearer '+bearerToken,
        'Accept': 'application/json'         
      })
    };
    return this.http.delete(url+'/'+id, httpOptions);
  }
  putUserApi(url,id,formData){                                            // Put Data
    let bearerToken = localStorage.getItem('login_token');

    let httpOptions = {
      headers: new HttpHeaders({       
        'Content-Type':  'application/json' ,
        'Authorization' : 'Bearer '+bearerToken,
        'Accept': 'application/json'         
      })
    };
    return this.http.put(url+'/'+id, formData, httpOptions);
  }


