import { Component, OnInit } from '@angular/core';
import { AlertController, ModalController } from "@ionic/angular";
import {  ModalpopupComponent } from "./../../common/modalpopup/modalpopup.component";
import { AuthenticationService } from './../../services/authentication.service';
import { Router } from  '@angular/router';
import { MainServicesService } from './../../services/main-services.service';
import $ from 'jquery';
import {PopoverController } from '@ionic/angular';
import { PopupComponent} from './../../common/popup/popup.component';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { ToastrService } from 'ngx-toastr';


@Component({
  selector: 'app-header-right-side',
  templateUrl: './header-right-side.component.html',
  styleUrls: ['./header-right-side.component.scss'],
})
export class HeaderRightSideComponent implements OnInit {
  leftSize: number;
  rightSize: number;
  showMenu: boolean = false;
  notificationsCount: number;
  values: any;
  unseenTokens = [];
  userName;
  profileImage;
  userId;
  getPermission;
  role;
  displayTokenAdmin;
  displayTokenCustomer;
  sucmsg;
  showMessage;
  notification;
  myCompOneObj;
  num;
  tokenCount:number;

  constructor(private router: Router, private modalController: ModalController,

     private notificationService: MainServicesService, private auth : AuthenticationService, private popctrl: PopoverController, 
               private alertCtrl: AlertController, private toastr: ToastrService) {

  
      this.getPermission = JSON.parse(localStorage.getItem('login_permission'));
      console.log(this.getPermission);
      
       this.notificationsCount = this.getPermission.notifications; 
       this.role = this.getPermission.role;
       this.userId = this.getPermission.id;

   this.notificationService.listen().subscribe(
     (m:any)=>{
       if(this.router.url.includes('/admin/tokens/'))
       {
        console.log(m);
        this.ngOnInit();
       }
      
     }
   )

  // for add token
   this.notificationService.tokenListen().subscribe(
    (m:any)=>{
       console.log(m);    
       this.ngOnInit();
    }
  )
       

   }

 
  logout(){
    localStorage.clear();
  	this.auth.signout().subscribe(
  		(data)=>{
        this.router.navigateByUrl('/login');
  		},
  		(error)=>{
        localStorage.clear();
        this.router.navigateByUrl('/login');
  			console.log(error);
  		}
	)
  }


  
  ngOnInit() { 

    this.notificationService.notifications().subscribe(
      (data) => {
        this.num=data['tokens']['token_data']['unseen']
        for (this.tokenCount = 0; this.tokenCount < this.num.length; this.tokenCount++) {
          
        }
        console.log(this.tokenCount);
      
      },
      (error) => {
        console.log(error);
      }
    )
    
    this.notificationService.getUserDetails(this.userId).subscribe(
      (result)=>{
        console.log(result);
        
      this.userName = result['user']['name'];
      this.notification = result['notifications'];
    
      
      this.profileImage = result['user']['image']['url'];
      },
      error=>{
      
        // if(error.status == 401){
        //   localStorage.clear()
        //   this.router.navigateByUrl('/login');
        // }
        
      }
      )
   }


  
    
  
  async openpopup(ev:any){
    const popover= await this.popctrl.create({
     component: PopupComponent,
     event:ev
     
    })
    return await popover.present();

  }



  async openModal() {
    const modal = await this.modalController.create({
      component: ModalpopupComponent,
      cssClass: 'my-profile-custom-class'
    });
    await modal.present();

    const { data: result, role } = await modal.onWillDismiss();

    this.sucmsg = result['message'];


    if (result['success'] == true) {
      this.showMessage = true;
      this.toastr.success(this.sucmsg);
      this.ngOnInit();
    }
  }

 

  manuToggle() {

    this.showMenu = !this.showMenu;

    if (this.showMenu == false) {

      if ($(window).width() < 992) {
        this.rightSize = 12;
      }
      else if ($(window).width() < 1200) {
        this.leftSize = 3;
        this.rightSize = 9;
      }
      else {
        this.leftSize = 2;
        this.rightSize = 10;

      }

      $('#firstdiv').removeClass("small-aside");
      $('#seconddiv').removeClass("big-right-side");


    }
    else {


      if ($(window).width() < 992) {
        this.rightSize = 12;
      }
      else if ($(window).width() < 1200) {
        this.leftSize = 1;
        this.rightSize = 11;
      }
      else {
        this.leftSize = 1;
        this.rightSize = 11;
      }






      $('#firstdiv').addClass("small-aside");
      $('#seconddiv').addClass("big-right-side");
    }
  }


  hide_mobile_menu() {
    $('#close-show-991').click(function () {

      $('#firstdiv').removeClass("small-aside");
    })
  }




  showMenuMob = true;
  mobileManuToggle() {
    $('#firstdiv').addClass("small-aside");
  }
 
  ionViewWillEnter() {


    if ($(window).width() < 992) {
      this.rightSize = 12;
    }
    else if ($(window).width() < 1367) {
      this.leftSize = 3;
      this.rightSize = 9;
    }

    else {
      this.leftSize = 2;
      this.rightSize = 10;
    }
  }
  
}
