import { Component, OnInit } from '@angular/core';
import { ModalController } from "@ionic/angular";
import { ModalpopupComponent } from "./../../common/modalpopup/modalpopup.component";
import { AuthenticationService } from './../../services/authentication.service';
import { Router } from  '@angular/router';
import { MainServicesService } from './../../services/main-services.service';
import $ from 'jquery';
import {PopoverController } from '@ionic/angular';
import { count } from 'rxjs/operators';

@Component({
  selector: 'app-popup',
  templateUrl: './popup.component.html',
  styleUrls: ['./popup.component.scss'],
})
export class PopupComponent implements OnInit {
  unseenTokens = [];
  userName;
  profileImage;
  userId;
  getPermission;
  notificationsCount;
  role;
  sucmsg;
  showMessage;

  href:any;
  IsEditToken:any;
  num;
  tokenCount:number;

  constructor(private router: Router, private modalController: ModalController,

    private notificationService: MainServicesService, private auth : AuthenticationService, private popctrl: PopoverController) {

      this.getPermission = JSON.parse(localStorage.getItem('login_permission'));
      //  this.userName = this.getPermission.name;
      //  this.profileImage = this.getPermission.image;
       this.notificationsCount = this.getPermission.notifications; 
       this.role = this.getPermission.role;
       this.userId = this.getPermission.id;

      this.notificationService.getUserDetails(this.userId).subscribe(
        (result)=>{
        this.userName = result['user']['name'];
        this.profileImage = result['user']['image']['url'];
        }
        )
     }

  async DismissClick() {
    await this.popctrl.dismiss();
    this.notificationService.filter('click');
    }
  ngOnInit() {
    this. getNotifications();
    this.href = this.router.url;
    let checkEdit = this.href.split("/");
    checkEdit['1'] == 'admin'?this.IsEditToken = '/'+checkEdit['1']+'/'+checkEdit['2']+'/'+checkEdit['3'] : this.IsEditToken = '/'+checkEdit['1']+'/'+checkEdit['2'];
  }
  notificationLength;



  getNotifications(){
    this.unseenTokens=[];
    this.notificationService.notifications().subscribe(
      (data) => {
        this.num=data['tokens']['token_data']['unseen']
        for (this.tokenCount = 0; this.tokenCount < this.num.length; this.tokenCount++) {
          
        }
        console.log(this.tokenCount);
        
        data['tokens']['token_data']['unseen'].forEach(unseen => {
          this.unseenTokens.push({
            id          : unseen._id,
            heading     : unseen.heading,
            token_type  : unseen.token_type,
            priority    : unseen.priority? unseen.priority : 'High',
            token_no    : unseen.token_no,
            status      : unseen.status
          })
        }); 
        this.notificationLength = this.unseenTokens.length;    

      },
      (error) => {
        console.log(error);
      }
    )

    
  }


}
